.gologin{
    position: absolute;
    left: 147px;
    top: 38px;
    a{
        font-size: 18px;
        text-decoration: none;
        color: gray;
        cursor: pointer;
        user-select: none;
        &:hover{
            color: red;
        }
    }
}
.top{
    width: 1000px;
    height: 50px;
    border-bottom: 2px solid #eee;
    margin: 0 auto;
    div{
        width: 250px;
        height: 50px;
        font-size: 16px;
        background-color: #ffffff;
        float: left;
        text-align: center;
        line-height: 50px;
        font-weight: bold;
        color: #999;
        span{
            display: inline-block;
            line-height: 30px;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background-color: #999;
            color: #fff;
        }
    }
    .active{
        color:#3e3e3e;
        border-bottom: 2px solid red;
        span{
            background-color: red;
        }
    }
    .complete{
        color: green;
        span{
            background-color: green;
        }
        border-bottom: 2px solid green;
    }
}
.testphone{
    width: 500px;
    height: 100px;
    margin: 0 auto;
    padding-top: 50px;
    label{
        width: 50px;
        height: 30px;
        line-height: 30px;
        margin-right: 30px;
        display: inline-block;
    }
    input{
        height: 30px;
        width: 300px;
        text-indent: 5px;
        outline: none;
    }
    .next{
        display: inline-block;
        border-radius: 3px;
        width: 200px;
        height: 35px;
        text-align: center;
        line-height: 35px;
        font-size: 18px;
        color: #fff;
        background-color: #eee;
        position: relative;
        left: 83px;
        top: 30px;
        cursor: pointer;
        user-select: none;
    }
    .nextactive{
        background-color:red;
    }
}
.writeinfor{
    display: none;
    .wrap{
        width: 400px;
        height: 550px;
        margin: 20px auto;
    }
    .wrap p{
        height: 40px;
        width: 380px;
        border: 1px solid #ccc;
        margin-left: 10px;
        line-height: 40px;
        margin-top: 10px;
        position: relative;
    }
    p label{
        display: inline-block;
        width: 80px;
        text-align: center;
        letter-spacing: 2px;
        font-size: 14px;
    }
    p span{
        display: inline-block;
        width: 20px;
        height: 20px;
        line-height: 20px;
        font-size: 20px;
        font-weight: bold;
        color: greenyellow;
        display: none;
    }
    p label:nth-child(4){
        letter-spacing: normal;
    }
    input{
        width: 250px;
        height: 30px;
        border: none;
    }
   .codeInput{
